.druki-media-remote-video-optimized {
  position: relative;
  overflow: hidden;
  height: 0;
  padding-bottom: 56.25%;
  border-radius: $border-radius-large;
  box-shadow: 0 3px 25px 0 rgba(22, 22, 22, .15);

  &__preview,
  &__iframe {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }

  &__preview {
    cursor: pointer;
  }

  &__thumbnail {
    position: relative;
    z-index: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__play {
    @include mdi-icon('play', $margin-size: 0);
    @include font-size(30px, 50px);
    text-align: center;
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 50px;
    color: white;
    background-color: rgba(black, 0.8);
  }

  &__loading {
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    display: none;
    transform: translate(-50%, -50%);
  }

  &__loading--active {
    display: block;
  }
}
