$icons: (
  'edit': 'lead-pencil',
);

.druki-title {
  $block: &;
  overflow: hidden;
  color: theme-color(on-primary);
  background-color: theme-color();

  &__inner {
    @include make-container();
    @include make-container-max-widths();
    padding-top: spacing-unit(8);
    padding-bottom: spacing-unit(8);
  }

  &__title {
    @include font-size(36px);
    font-weight: bold;
    margin: unset;
  }

  &__links {
    display: flex;
    padding-top: spacing-unit(6);
  }

  &__links-item:not(:last-child) {
    margin-right: spacing-unit(2);
  }

  @each $modifier, $icon in $icons {
    &__links-link--#{$modifier} {
      @include mdi-icon($icon);
    }
  }

  &__links-link {
    display: flex;
    align-items: center;
    padding: spacing-unit() spacing-unit(2);
    color: theme-color(secondary);
    border-radius: $border-radius-base $border-radius-base 0 0;
    background-color: white;
    box-shadow: 0 0 6px 3px rgba(0, 0, 0, 0.05);

    &:hover {
      color: $link-color;
    }
  }

  @include media-breakpoint-up(lg) {
    &--with-links {
      #{$block}__inner {
        padding-bottom: unset;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__title {
      @include font-size(28px);
    }

    &__links {
      display: none;
    }
  }
}
